import React, {useEffect, useState} from 'react'
import styled from 'styled-components'
import PageSection from 'components/PageSection'
import useTheme from 'hooks/useTheme'
import { PageMeta } from 'components/Layout/Page'
import { useCookies } from "react-cookie";
import { useLocation } from "react-router-dom";
import Hero from './components/Hero'

const addressRegex = RegExp(`^[?]ref[=]0x[a-zA-Z0-9]{40}$`)

const StyledHeroSection = styled(PageSection)`
  padding-top: 16px;

  ${({ theme }) => theme.mediaQueries.md} {
    padding-top: 48px;
  }
`

const Home: React.FC = () => {
  const { theme } = useTheme();
  const [location] = useState(useLocation());
  const [cookies, setCookie] = useCookies(['refAddress']);

  useEffect(() => {
      if (cookies.refAddress === undefined && addressRegex.test(location.search)) {
          setCookie(
              'refAddress',
              location.search.replace("?ref=", ""),
              {
                  path: '/',
                  expires: new Date(Date.UTC(new Date().getUTCFullYear() + 420, new Date().getUTCMonth()))
              });
      }
  });

  return (
    <>
      <PageMeta />
      <StyledHeroSection
        innerProps={{ style: { margin: '0', width: '100%', lineHeight: 0 } }}
        background={
          theme.isDark
            ? 'radial-gradient(103.12% 50% at 50% 50%, #304FAC 0%, #0d162b 100%)'
            : 'linear-gradient(139.73deg, #E6FDFF 0%, #F3EFFF 100%)'
        }
        index={2}
        hasCurvedDivider={false}
      >
        <Hero />
      </StyledHeroSection>
    </>
  )
}

export default Home
