import React, {useEffect, useState} from 'react'
import styled, { keyframes } from 'styled-components'
import { Flex, Heading, Button, Link } from '@pancakeswap/uikit'
import { useWeb3React } from '@web3-react/core'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import { SlideSvgDark, SlideSvgLight } from './SlideSvg'
import { getSrcSet } from './CompositeImage'
import {
  getTimeRemaining,
  isCountdownDone
} from "../../../utils/countdownCalculations";

const flyingAnim = () => keyframes`
  from {
    transform: translate(0,  0px);
  }
  50% {
    transform: translate(-5px, -5px);
  }
  to {
    transform: translate(0, 0px);
  }  
`

const BgWrapper = styled.div`
  z-index: -1;
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0px;
  left: 0px;
`

const InnerWrapper = styled.div`
  position: absolute;
  width: 100%;
  bottom: -3px;
`

const ChonkWrapper = styled.div`
  width: 100%;
  animation: ${flyingAnim} 3.5s ease-in-out infinite;
`

const imagePath = '/images/home/lazy-chonk/'
const imageSrc = 'chonk'

const Hero = () => {
  const [countdown, setCountdown] = useState(getTimeRemaining());
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const { theme } = useTheme()

  useEffect(() => {
    const timer = !isCountdownDone() && setInterval(() => setCountdown(getTimeRemaining()), 1000);
    return() => clearInterval(timer);
  }, []);

  return (
    <>
      <BgWrapper>
        <InnerWrapper>{theme.isDark ? <SlideSvgDark width="100%" /> : <SlideSvgLight width="100%" />}</InnerWrapper>
      </BgWrapper>
      <Flex
        position="relative"
        flexDirection={['column-reverse', null, null, 'row']}
        alignItems={['flex-end', null, null, 'center']}
        justifyContent="center"
        mt={[account ? '280px' : '50px', null, 0]}
        id="homepage-hero"
      >
        <Flex flex="1" flexDirection="column">
          <Heading scale="xxl" color="secondary" mb="24px">
            {t('Finance is made of lazy chonks.')}
          </Heading>
          <Heading scale="md" mb="24px">
            {`Earn BNB while being lazy in ${countdown}`}
          </Heading>
          <Flex>
            <Link external href="https://thechonkxxx.gitbook.io/chonk.fi-whitepaper-and-wiki/">
              <Button
                  variant='primary'
                  mr="8px"
              >
                {t('Open Wiki')}
              </Button>
            </Link>
            <Button
                disabled
                variant='secondary'
            >
              {t('Launch App')}
            </Button>
          </Flex>
        </Flex>
        <Flex
          height={['192px', null, null, '100%']}
          width={['192px', null, null, '100%']}
          flex={[null, null, null, '1']}
          mb={['24px', null, null, '0']}
          position="relative"
        >
          <ChonkWrapper>
            <img src={`${imagePath}${imageSrc}.png`} srcSet={getSrcSet(imagePath, imageSrc)} alt={t('Lazy chonk')} />
          </ChonkWrapper>
        </Flex>
      </Flex>
    </>
  )
}

export default Hero
